/* media */
.body__wrapper {
  box-sizing: border-box;
  max-width: 1200px;
  height: 100%;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
  margin: 0 auto;
  padding: 0;
  font-family: "PT sans", "Arial", sans-serif;
  text-transform: uppercase;
  background-color: #ffffff;
}
.page-header {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0;
}
.page-header__wrapper {
  position: relative;
  min-height: 56px;
}
.page-header__logo {
  display: block;
  width: 102px;
  height: 84px;
  position: absolute;
  top: 0;
  left: calc(50% - 51px);
  z-index: 2;
}
.page-header__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
  display: block;
  width: 65px;
  height: 56px;
  background-color: #ffffff;
  cursor: pointer;
  border: none;
}
.page-header__toggle:hover {
  opacity: 0.6;
}
.page-header__toggle:active {
  opacity: 0.3;
}
.page-header__toggle--closed {
  background-image: url("../img/burger-menu.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.page-header__toggle--opened {
  background-image: url("../img/menu-cross.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-color: #81B3D2;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    width: 186px;
    height: 97px;
    left: calc(50% - 93px);
  }
}
.main-nav {
  position: absolute;
  width: 100%;
  top: 0;
  display: flex;
  flex-direction: column;
  min-height: 56px;
  background-color: #81B3D2;
}
.main-nav--opened {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  z-index: 2;
}
.main-nav--closed {
  display: none;
}
.main-nav__list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #81B3D2;
}
.main-nav__list a {
  display: block;
  padding: 18px 0;
  padding-bottom: 13px;
  font-size: 18px;
  line-height: 24px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #81B3D2;
  border-top: 1px solid #92cbee;
  border-bottom: 1px solid #658ea7;
}
.main-nav__list a:hover {
  background-color: #669EC0;
}
.main-nav__list a:active {
  background-color: #5496BD;
  opacity: 0.3;
}
.main-nav__item {
  font-weight: bold;
  font-size: 18px;
  line-height: 40px;
}
.main-nav__link {
  text-decoration: none;
  color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav {
    display: flex;
    background-color: #ffffff;
  }
  .main-nav--opened {
    z-index: 1;
  }
  .main-nav__wrapper {
    display: flex;
    justify-content: space-between;
    width: 720px;
    margin: 0 auto;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0;
    padding: 0;
    background-color: #ffffff;
  }
  .main-nav__list a {
    color: #000000;
    border: 0;
    background-color: #ffffff;
    font-size: 14px;
    line-height: 20px;
    width: 153px;
  }
  .main-nav__list a:hover {
    background-color: #ffffff;
    color: #81B3D2;
  }
  .main-nav__list a:active {
    background-color: #ffffff;
    opacity: 0.3;
  }
  .main-nav__item--logo {
    display: block;
    width: 156px;
  }
  .main-nav__link {
    list-style: none;
    color: #000000;
  }
  .main-nav__list .main-nav__link--active {
    color: #766357;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    background-color: #ffffff;
  }
  .main-nav__wrapper {
    display: flex;
    justify-content: space-between;
    width: 1170px;
    margin: 0 auto;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0;
    padding: 0;
