body {
  width: 100%;
  background-color: @gray85;
  padding: 0;
  margin: 0;
}

main {
  flex-grow: 1;
}

img {
  display: block;
  width: 100%;
  height: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}
